import React, { useState, useEffect } from "react";
import Countries from "./countries";
import Forms from "./forms";

function Home() {
  const [countries, setCountries] = useState([]);
  const [search, setSearch] = useState("");
  const [choose, setChoose] = useState("");
  const [url, setUrl] = useState("https://restcountries.eu/rest/v2/all");

  //const url = "https://restcountries.eu/rest/v2/all";
  //urlSelect = `https://restcountries.eu/rest/v2/region/${region}`;

  useEffect(() => {
    const getItems = () => {
      fetch(url)
        .then((response) => response.json())
        .then((data) => {
          const fewData = data.splice(0, 12);
          console.log(fewData);
          setCountries(fewData);
        });
    };

    getItems();
  }, [url]);

  const onChange = (e) => {
    setSearch(e.target.value);
  };

  const onSelect = (e) => {
    setChoose(e.target.value);
    let region = e.target.value;
    let newUrl = `https://restcountries.eu/rest/v2/region/${region}`;
    region !== "" ? setUrl(newUrl) : setUrl(url);
  };
  return (
    <div className="container">
      <Forms
        handleChange={onChange}
        search={search}
        select={choose}
        handleSelect={onSelect}
      />

      <div className="holder">
        <p className="count">{countries.length} countries</p>
        <div className="item-container">
          {countries.map((country) => (
            <Countries
              key={country.alpha3Code}
              flag={country.flag}
              name={country.name}
              capital={country.capital}
              population={country.population}
              region={country.region}
              id={country.alpha3Code}
            />
          ))}
        </div>
      </div>

      <div className="footer">
        <p>
          copyright &copy; <a href="https://github.com/horler408">HorlerTech</a>
          <span id="date">2020</span>. All right reserved
        </p>
      </div>
    </div>
  );
}

export default Home;
